<template>
  <div>
    <ul class="cart__orders">
      <OrderProductItem v-for="item in products" :key="item.productId" :product="item" />
    </ul>
    
    <div class="cart__total">
      <p>Доставка: <b>500 ₽</b></p>
      <p>Итого: <b>{{ productsAmount }}</b> позиций на сумму <b>{{ totalPrice | numberFormat }} ₽</b></p>
    </div>
  </div>
</template>

<script>
  import OrderProductItem from '@/components/OrderProductItem'
  import numberFormat from '@/helpers/numberFormat'

  export default {
    name: 'order-product-list',
    components: {OrderProductItem},
    props: ['products', 'totalPrice'],
    data(){
      return {
        productsAmount: ''
      }
    },
    filters: {numberFormat},
    created(){
      this.productsAmount = this.products.length
    }
  }
</script>