<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" href="#" :to="{name: 'main'}">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" href="#" :to="{name: 'cart'}">
            Корзина
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Оформление заказа
          </a>
        </li>
      </ul>

      <h1 class="content__title">
        Заказ оформлен <span>№ {{ this.$route.params.id }}</span>
      </h1>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <p class="cart__message">
            Благодарим за&nbsp;выбор нашего магазина. На&nbsp;Вашу почту придет письмо с&nbsp;деталями заказа. 
            Наши менеджеры свяжутся с&nbsp;Вами в&nbsp;течение часа для уточнения деталей доставки.
          </p>

          <div class="loader" v-if="orderInfo === false"></div>
          <ul class="dictionary" v-else>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Получатель
              </span>
              <span class="dictionary__value">
                {{ orderInfo.name }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Адрес доставки
              </span>
              <span class="dictionary__value">
                {{ orderInfo.address }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Телефон
              </span>
              <span class="dictionary__value">
                {{ orderInfo.phone | numberFormat }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Email
              </span>
              <span class="dictionary__value">
                {{ orderInfo.email }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Способ оплаты
              </span>
              <span class="dictionary__value">
                картой при получении
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Комментарий
              </span>
              <span class="dictionary__value">
                {{ orderInfo.comment }}
              </span>
            </li>
          </ul>
        </div>

        <div class="cart__block">

          <OrderProductList v-if="products" :products="products" :total-price="totalPrice" />
          <div class="loader" v-else></div>

        </div>
      </form>
    </section>
  </main>
</template>
<script>
  import numberFormat from '@/helpers/numberFormat'
  import OrderProductList from '@/components/OrderProductList'

  export default {
    name: 'order-info-page',
    components: {OrderProductList},
    data(){
      return {
        orderInfo: false
      }
    },
    created(){
      if(this.$store.state.orderInfo && this.$store.state.orderInfo.id === this.$route.params.id){
        this.orderInfo = this.$store.state.orderInfo
        return
      }
      this.$store.dispatch('loadOrderInfo', this.$route.params.id)
      .then(()=>{
        this.orderInfo = this.$store.state.orderInfo
      })
    },
    computed: {
      products(){
        if(this.orderInfo){

          return  this.orderInfo.basket.items.map((item) => ({
            amount: item.quantity,
            productId: item.product.id,
            product: {
              title: item.product.title,
              price: item.product.price,
            }
          }))

        } else {
          return null
        }        
      },
      totalPrice(){
        return this.orderInfo.totalPrice
      }
    },
    filters: {numberFormat},
  }
</script>