<template>
	<div class="form__counter">
		<button type="button" aria-label="Убрать один товар" @click="decreaseProductAmount">
			<svg width="12" height="12" fill="currentColor">
				<use xlink:href="#icon-minus"></use>
			</svg>
		</button>

		<input type="text" v-model.number="productAmount" @input="syncProductAmount">

		<button type="button" aria-label="Добавить один товар" @click="increaseProductAmount">
			<svg width="12" height="12" fill="currentColor">
				<use xlink:href="#icon-plus"></use>
			</svg>
		</button>
	</div>
</template>

<script>
	export default {
		name: 'product-amount-changing-item',
		props: ['productStartAmount'],
		data(){
			return {
				productAmount: this.productStartAmount
			}
		},
		methods: {
      syncProductAmount() {
        this.$emit('updateProductAmount', this.productAmount)
      },
			increaseProductAmount(){
				this.productAmount++
				this.$emit('updateProductAmount', this.productAmount)
			},
			decreaseProductAmount(){
				if(this.productAmount > 0) {
					this.productAmount--
					this.$emit('updateProductAmount', this.productAmount)
				}				
			},
		},

	}
</script>