<template>
	<ul class="catalog__pagination pagination">
		<li class="pagination__item">
			<a class="pagination__link pagination__link--arrow" :class="{'pagination__link--disabled' : page === 1}" @click.prevent="paginate(page - 1)" aria-label="Предыдущая страница">
				<svg width="8" height="14" fill="currentColor">
					<use xlink:href="#icon-arrow-left"></use>
				</svg>
			</a>
		</li>

		<li class="pagination__item" v-for="pageNumber in pages" :key="pageNumber">
				<a class="pagination__link" :class="{'pagination__link--current' : pageNumber === page}" href="#" @click.prevent="paginate(pageNumber)">
					{{ pageNumber }}
				</a>
		</li>

		<li class="pagination__item">
			<a class="pagination__link pagination__link--arrow"  :class="{'pagination__link--disabled' : page === pages}" @click.prevent="paginate(page + 1)" href="#" aria-label="Следующая страница">
				<svg width="8" height="14" fill="currentColor">
					<use xlink:href="#icon-arrow-right"></use>
				</svg>
			</a>
		</li>
	</ul>
</template>

<script>
	export default {
		name: 'base-pagination',
		model: {
			prop: 'page',
			event: 'paginate'
		},

		props: {
			page: Number,
			count: Number,
			perPage: Number
		},

		computed: {
			pages() {
				return Math.ceil(this.count / this.perPage)
			},

		},

		methods: {
			paginate(page) {
				this.$emit('paginate', page);
			},
		}
	}
</script>

<style>
	.pagination__link:not(.pagination__link--disabled) {
		cursor: pointer;
		border-color: #000;
	}
	.pagination__link--disabled {
		pointer-events: none;
	}
	.pagination__link svg {
		pointer-events: none;
	}
</style>