<template>
	<li class="cart__item product" >
		<div class="product__pic">
			<img :src="item.product.imageUrl" width="120" height="120" :alt="item.product.title">
		</div>
		<h3 class="product__title">
			{{ item.product.title }}
		</h3>
		<span class="product__code">
			Артикул: {{ item.product.id }}
		</span>

		<div class="product__counter form__counter">
			<ProductAmountChangingItem @updateProductAmount="onUpdateCartProductAmount" :product-start-amount="item.amount" />
		</div>

		<b class="product__price">
			{{ (item.amount * item.product.price) | numberFormat }} ₽
		</b>

		<button class="product__del button-del" type="button" aria-label="Удалить товар из корзины" @click.prevent="removeProductFromBasket(item.productId)">
			<svg width="20" height="20" fill="currentColor">
				<use xlink:href="#icon-close"></use>
			</svg>
		</button>
	</li>
</template>
<script>
	import numberFormat from '@/helpers/numberFormat'
	import { mapActions } from 'vuex'
	import ProductAmountChangingItem from '@/components/ProductAmountChangingItem'	
	export default {
		name: 'cart-item',
		props: ['item'],
		components: {ProductAmountChangingItem},
		filters: {numberFormat},
		computed: {			
			amount: {
				get(){
					return this.item.amount
				},
				set(value){
					this.$store.dispatch('syncCartProductAmount', {productId: this.item.productId, amount: value})
				}
			}
		},
		methods: {
			...mapActions(['removeProductFromBasket']),			
			onUpdateCartProductAmount(amount) {    
				this.productAmount = amount
				this.amount = amount
			},
		},
	}
</script>