<template>
	<main class="content container">
		<div class="content__top content__top--catalog">
			<h1 class="content__title">
				Каталог
			</h1>
			<span class="content__info">
				товаров: {{ countProducts }} 
			</span>
		</div>    
		<div class="content__catalog">

			<ProductFilter :price-from.sync="filterPriceFrom" :price-to.sync="filterPriceTo" :category-id.sync="filterCategoryId" :choosen-color.sync="filterColor" />

			<section class="catalog">   

        <div class="loader" v-if="productsLoading">Загрузка товаров...</div>
        <div class="column-flex" v-if="productsLoadingFailed">
          <span class="error-message">Произошла ошибка при загрузке товаров...</span>
          <button @click.prevent="loadProducts">Попробовать ещё раз</button>
        </div>

				<ProductList :products="products"  />

				<BasePagination v-model="page" :count="countProducts" :per-page="productsPerPage" />

			</section>      
		</div>    
  </main>
</template>

<script>
  import ProductList from '@/components/ProductList'
  import BasePagination from '@/components/BasePagination'
  import ProductFilter from '@/components/ProductFilter'
  import axios from 'axios'
  import {BASE_API_URL} from '@/data/config'

  export default {
    name: 'main-page',
    components: {
      ProductList,
      BasePagination,
      ProductFilter
    },
    data() {
      return {
        page: 1,
        productsPerPage: 3,
        filterPriceFrom: 0,
        filterPriceTo: 0,
        filterCategoryId: 0,
        filterColor: 0,
        productsData: null,
        productsLoading: false,
        productsLoadingFailed: false,
      }
    },
    computed: {
      products() {
        return this.productsData ? this.productsData.items.map(product => {
          return {
            ...product,
            imageUrl: product.image.file.url
          }
        }
        )
        : []
      },
      countProducts() {
        return this.productsData ? this.productsData.pagination.total : 0
      }
    },
    methods: {
      loadProducts(){
        this.productsLoading = true
        this.productsLoadingFailed = false
        clearTimeout(this.loadProductsTimer)        //чтобы не посылать трижды один и тот же запрос,если фильтров будет активировано 3
        this.loadProductsTimer = setTimeout(() => { //чтобы не посылать трижды один и тот же запрос,если фильтров будет активировано 3
          axios.get(`${BASE_API_URL}/api/products`, {
            params: {
              page: this.page,
              limit: this.productsPerPage,
              categoryId: this.filterCategoryId,
              minPrice: this.filterPriceFrom,
              maxPrice: this.filterPriceTo,
              colorId: this.filterColor,
            }
          })
          .then(response => this.productsData = response.data)
          .catch(() => this.productsLoadingFailed = true)
          .then(() => this.productsLoading = false)
        }, 0)
      }
    },
    watch: {
      page(){
        this.loadProducts()
      },
      filterCategoryId(){
        this.loadProducts()
      },
      filterPriceFrom(){
        this.loadProducts()
      },
      filterPriceTo(){
        this.loadProducts()
      },
      filterColor(){
        this.loadProducts()
      }
    },
    created(){
      this.loadProducts()
    }
  }
</script>
<style>
  .column-flex {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 50px;
  }
  .error-message {
    margin-bottom: 30px;
    font-weight: 500;
    font-size:  25px;
  }
  .my-btn {
    padding: 15px 23px;
    font-size:  18px;
    cursor: pointer;
  }

  .loader,
  .loader:before,
  .loader:after {
    border-radius: 50%;
    width: 2.5em;
    height: 2.5em;
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both;
    -webkit-animation: load7 1.8s infinite ease-in-out;
    animation: load7 1.8s infinite ease-in-out;
  }
  .loader {
    color: #65abf8;
    font-size: 10px;
    margin: 80px auto;
    position: relative;
    text-indent: -9999em;
    -webkit-transform: translateZ(0);
    -ms-transform: translateZ(0);
    transform: translateZ(0);
    -webkit-animation-delay: -0.16s;
    animation-delay: -0.16s;
  }
  .loader:before,
  .loader:after {
    content: '';
    position: absolute;
    top: 0;
  }
  .loader:before {
    left: -3.5em;
    -webkit-animation-delay: -0.32s;
    animation-delay: -0.32s;
  }
  .loader:after {
    left: 3.5em;
  }
  @-webkit-keyframes load7 {
    0%,
    80%,
    100% {
      box-shadow: 0 2.5em 0 -1.3em;
    }
    40% {
      box-shadow: 0 2.5em 0 0;
    }
  }
  @keyframes load7 {
    0%,
    80%,
    100% {
      box-shadow: 0 2.5em 0 -1.3em;
    }
    40% {
      box-shadow: 0 2.5em 0 0;
    }
  }
</style>