<template>  
  <li class="catalog__item">      
    <router-link class="catalog__pic"  :to="{name: 'product', params: {id: product.id}}">
      <img :src="product.imageUrl" :alt="product.title">
    </router-link>
    <h3 class="catalog__title">
      <a href="#">
        {{ product.title }}
      </a>
    </h3>
    <span class="catalog__price">
      {{ product.price | numberFormat }} ₽
    </span>
    
    <ul class="colors colors--black">
      <li class="colors__item" v-for="productColor in product.colors" :key="`product${product.id}_${productColor.title}`">
        <label class="colors__label">
          <input class="colors__radio sr-only" type="radio" :value="getColor(productColor.title)"  v-model="color">
          <span class="colors__value" :style="`background-color: ${getColor(productColor.title)}`">
          </span>
        </label>
      </li>
    </ul>
  </li>  
</template>


<script>
  import colorsList from '@/data/colors'
  import gotoPage from '@/helpers/gotoPage'
  import numberFormat from '@/helpers/numberFormat'
  export default {
    name: 'product-item',
    data() {
      return {
        color: '',
      }
    },
    props: ["product"],
    methods: {
      getColor(name) {
        for (let color of colorsList) {
          if (color.colorName === name) return color.value
        }
      },
      gotoPage
    },
    filters: {
      numberFormat
    },
    mounted() {
      this.color = this.getColor(this.product.colors[0].title) 
    }
  }
</script>

<style>
  .colors__value {
    outline: 1px solid black;
    outline-offset: -1px;
  }
</style>